@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
*{
  padding:0px;
  margin:0px;
  box-sizing: border-box;
}
body{
  display: grid;
  place-items: center;
  height:100vh;
  width:100vw;
  background-color: var(--black-main);;
  --green-main:#39FF14;
  --green-primary:#008F11;
  --green-secondary:#003B00;
  --black-main: #0D0208;
  --pink-main:#DA14FF;
  --red-main:#ff073a;
  --yellow-main:#ccff15;
  
}
.app__body{
  height:98vh;
  width:98vw;
  background-color:#F1F1FF;
  border: .5vh solid #aaa;
  background:linear-gradient(120deg,var(--black-main),#aaa);
  border-radius: 2vh;

}

.app__header{
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
     font-family: 'Press Start 2P', cursive;
    text-transform: capitalize;
    padding: 5vh;
    border-bottom:#aaa solid .5vh;
    background: linear-gradient(120deg,#aaa,black);
    height:20vh;
   
  }
  .app__header__logo{
    color:var(--green-main);
    font-size: 4vh;
    margin: 1vh;
    text-shadow: 1vh .1vh 1vh #aaa;
  }
  .header__text {
    color :var(--pink-main);
    text-shadow: .5vh .5vh .5vh black;
    font-size: smaller;
  }
  .logo__container{
      display: grid;
      place-items: center;
      border: 1vh solid var(--pink-main);
      width:70vw;
      margin: auto;
      padding: 2vh;
      margin-top: 5vh;
      border-radius: 3vh;
      box-shadow: .5vh .5vh 6vh .5vh black;
  }

  .logo{
    width:20vw!important;
    height: 36vh;
    object-fit: contain;
  }

  li{
      display: inline;
  }
  ul{
      margin: 3vh;
      display: flex;
      justify-content: space-evenly;
  }
  .signin{
    
      text-decoration: none;
      font-family: 'Press Start 2P', cursive;
      border: .4vh solid ;
      color:gold;
      padding: 1vh;
      border-radius: 2vh;
      font-weight: 500 ;
      font-size: small;
      box-shadow: .5vh .5vh 6vh .5vh black;
  }
  .signup{
    text-decoration: none;
    font-family: 'Press Start 2P', cursive;
    border: .4vh solid ;
      color:var(--red-main);
      padding: 1vh;
      border-radius: 2vh;
      font-weight: 500;
      font-size: small;
      box-shadow: .5vh .5vh 6vh .5vh black;
  }
  .nav{
      min-height: 7vh;
      padding: 2vh;
      
  }

.signinformheader{
    border: .5vh solid #aaa;
    width:90vw;
    border-radius: 2vh;
    height:90vh;
    background: linear-gradient(120deg,black,#aaa);
}
.robot{
    width:20vw;
    height:20vh;
    object-fit: contain;
}
.robo{
    display: flex;
    justify-content: center;
    border: 1vh solid var(--pink-main);
    border-radius: 2vh;
    padding:5vh;
    width: 80vw;
    margin-top: 2vh;
    margin-left:5vw;
    margin-right: 5vh;
    box-shadow: .5vh .5vh 6vh .5vh black;
    background: linear-gradient(120deg,#aaa,black);
}
.form{
    display: flex;
    justify-content: center;
    color:var(--green-main);
    font-family: 'Press Start 2P', cursive;
    text-align: center;
    margin-top: .5vh;
}
.input{
    background:linear-gradient(120deg,var(--pink-main),var(--green-main));
    opacity: .8;
    height:3.5vh;
    width:30vh;
    border-radius: 4vh;
    box-shadow: .5vh .5vh 6vh .5vh black;
}
.input:focus{
    background:linear-gradient(120deg,var(--pink-main),var(--green-main));
    opacity: .6;
    height:3.5vh;
    width:30vh;
    font-family: 'Press Start 2P', cursive;
    color:#ccc;
}
.input:active{
    background:linear-gradient(120deg,var(--pink-main),var(--green-main));
    opacity: .6;
    height:3.5vh;
    width:30vh;
    font-family: 'Press Start 2P', cursive;
    color:#ccc;
}

.btn{
    width:30vw;
    height:4.5vh;
    border-radius: 5vh;
    border: none;
    box-shadow: .5vh .5vh 6vh .5vh black;
    cursor: pointer;
    background-color: var(--green-main);
    font-size: smaller;
}
.button{
display: flex;
justify-content: center;
padding: .5vh;
}
