
.signinformheader{
    border: .5vh solid #aaa;
    width:90vw;
    border-radius: 2vh;
    height:90vh;
    background: linear-gradient(120deg,black,#aaa);
}
.robot{
    width:20vw;
    height:20vh;
    object-fit: contain;
}
.robo{
    display: flex;
    justify-content: center;
    border: 1vh solid var(--pink-main);
    border-radius: 2vh;
    padding:5vh;
    width: 80vw;
    margin-top: 2vh;
    margin-left:5vw;
    margin-right: 5vh;
    box-shadow: .5vh .5vh 6vh .5vh black;
    background: linear-gradient(120deg,#aaa,black);
}
.form{
    display: flex;
    justify-content: center;
    color:var(--green-main);
    font-family: 'Press Start 2P', cursive;
    text-align: center;
    margin-top: .5vh;
}
.input{
    background:linear-gradient(120deg,var(--pink-main),var(--green-main));
    opacity: .8;
    height:3.5vh;
    width:30vh;
    border-radius: 4vh;
    box-shadow: .5vh .5vh 6vh .5vh black;
}
.input:focus{
    background:linear-gradient(120deg,var(--pink-main),var(--green-main));
    opacity: .6;
    height:3.5vh;
    width:30vh;
    font-family: 'Press Start 2P', cursive;
    color:#ccc;
}
.input:active{
    background:linear-gradient(120deg,var(--pink-main),var(--green-main));
    opacity: .6;
    height:3.5vh;
    width:30vh;
    font-family: 'Press Start 2P', cursive;
    color:#ccc;
}

.btn{
    width:30vw;
    height:4.5vh;
    border-radius: 5vh;
    border: none;
    box-shadow: .5vh .5vh 6vh .5vh black;
    cursor: pointer;
    background-color: var(--green-main);
    font-size: smaller;
}
.button{
display: flex;
justify-content: center;
padding: .5vh;
}