.app__header{
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
     font-family: 'Press Start 2P', cursive;
    text-transform: capitalize;
    padding: 5vh;
    border-bottom:#aaa solid .5vh;
    background: linear-gradient(120deg,#aaa,black);
    height:20vh;
   
  }
  .app__header__logo{
    color:var(--green-main);
    font-size: 4vh;
    margin: 1vh;
    text-shadow: 1vh .1vh 1vh #aaa;
  }
  .header__text {
    color :var(--pink-main);
    text-shadow: .5vh .5vh .5vh black;
    font-size: smaller;
  }
  .logo__container{
      display: grid;
      place-items: center;
      border: 1vh solid var(--pink-main);
      width:70vw;
      margin: auto;
      padding: 2vh;
      margin-top: 5vh;
      border-radius: 3vh;
      box-shadow: .5vh .5vh 6vh .5vh black;
  }

  .logo{
    width:20vw!important;
    height: 36vh;
    object-fit: contain;
  }

  li{
      display: inline;
  }
  ul{
      margin: 3vh;
      display: flex;
      justify-content: space-evenly;
  }
  .signin{
    
      text-decoration: none;
      font-family: 'Press Start 2P', cursive;
      border: .4vh solid ;
      color:gold;
      padding: 1vh;
      border-radius: 2vh;
      font-weight: 500 ;
      font-size: small;
      box-shadow: .5vh .5vh 6vh .5vh black;
  }
  .signup{
    text-decoration: none;
    font-family: 'Press Start 2P', cursive;
    border: .4vh solid ;
      color:var(--red-main);
      padding: 1vh;
      border-radius: 2vh;
      font-weight: 500;
      font-size: small;
      box-shadow: .5vh .5vh 6vh .5vh black;
  }
  .nav{
      min-height: 7vh;
      padding: 2vh;
      
  }